* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}
:root {
  --baseColor: #fff;
  /* 주조색(배경색) - 약 70% 사용 */
  --dominantColor: black;
  /* 보조색 - 20% 사용 */
  --accentColor: royalblue;
}
h2 {
  margin-bottom: 1em;
}
h3 {
  text-align: center;
  font: 1em;
  height: 2em;
  color: var(--accentColor);
  margin-top: 1em;
  margin-bottom: 1em;
}
article {
  padding: 1em;
}
article ul,
article ol {
  padding-left: 2em;
}
table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}
th,
td {
  border: 0.1em solid var(--dominantColor);
  padding: 0.5em;
}
thead th {
  background-color: #2c2c2c;
  color: #fff;
}
tbody th {
  text-align: left;
  font-weight: normal;
}
caption {
  text-align: left;
  height: 2em;
}
.good {
  text-align: center;
}
